.main
{
    align-items: center;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}
.check-btn{
    border: 2px rgb(35, 7, 61);
    font-size: 35px;
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    color: blueviolet;
    border-radius: 6px;
    box-shadow: 2px 4px 2px;
    box-sizing: border-box;
    background-color: rgba(63, 61, 64, 0);
}
.high
{
    color: rgb(255, 255, 255);
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    font-size: 75px;
    transition: all 0.5s;
}
